//    import mockdata from '@/util/mockdata.js'

    export default {
        data(){

            var validatePassword = (rule, value, callback) => {
                if (this.registerForm.password !== this.registerForm.checkPassword) {
                    callback(new Error('两次输入的密码不同!'));
                } else {
                    callback();
                }
            };

            return {

                registerForm: {
                    username: '',
                    password: '',
                    checkPassword: '',
                    phone: '',
                    verifyCode: '',
                },

                rules: {
                    username: [
                        {
                            required: true, message: '请输入用户名', trigger: 'blur',
                        }, {
                            min: 4, max: 10, message: '长度在 4 - 10 之间', trigger: 'blur',
                        }
                    ],
                    password: [
                        {
                            required: true, message: '请输入密码', trigger: 'blur',
                        },
//                        {
//                            validator: validatePassword, trigger: 'blur',
//                        }
                    ],
                    checkPassword: [
                        {
                            required: true, message: '重新确认密码', trigger: 'blur',
                        }, {
                            validator: validatePassword, trigger: 'blur',
                        }
                    ],
                    phone: [
                        {
                            required: true, message: '请输入电话号码', trigger: 'blur',
                        }
                    ],
                    verifyCode: [
                        {
                            required: true, message: '请输入验证码', trigger: 'blur',
                        }
                    ],

                }
            }
        },

        methods: {
            register(formName){
                console.log(formName)
                let _this = this;

                this.$refs['registerForm'].validate((valid) => {
                        if (valid) {

                            axios.post('/register', {
                                'username': formName.username,
                                'password': formName.password,
                                'phone': formName.phone
                            }).then(function (response) {
                                var data = response.data;
                                console.log(data);
                                if (data.isRegister) {
                                    _this.$message({
                                        message: '注册成功~',
                                        type: "success"
                                    });

                                    setTimeout(() => {
                                        _this.$router.push({path: '/home'})
                                    }, 3000);

                                } else {
                                    _this.$message({
                                        showClose: true,
                                        message: data.message,
                                        type: 'error'
                                    })
                                }

                            }).catch(function (error) {
                                console.log(error);
                            })

                        }
                        else {
                            console.log('error submit!!');
                            return false;
                        }
                    }
                );

            },

            resetForm(formName) {
                this.$refs[formName].resetFields();
            }

        },
//        beforeRouteLeave(to, from, next){
//
//        },

    }




